input {
  @include font-features;
  @include font-smoothing(on);
  font-family: $sans-serif;
  border-radius: 0;
}

input[type=search] {
  -webkit-appearance: none;
}

button {
  @include font-features;
  background: transparent;
  border: 0;

  &:focus {
    outline: thin dotted;
  }
}

